@import url(//db.onlinewebfonts.com/c/d7e8a95865396cddca89b00080d2cba6?family=SoDo+Sans+SemiBold);

* {
    margin: 0;
    padding: 0;
}

.corpo {
    margin: 0 auto;
    overflow-x: hidden;
}

.carrossel {
    margin: 0 2.938rem 2rem 2.938rem;
}

#dimensionamento {
    height: 11.963rem;
    width: 90rem;
    margin: 2rem auto;
}

#dimensionamento p {
    width: 56rem;
}

#dimensionamento2 {
    width: 45rem;
}

#dimensionamento2 p {
    width: 31.25rem;
}

#dimensionamento3 p {
    width: 31.25rem;
}

#container_maior {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 2rem auto 0 auto;
}

.divParaImagem1 {
    width: 45rem;
}

.divParaImagem1 img {
    width: 45rem;
}

.estilo_compartilhado {
    background-color: #d4e9e2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.estilo_compartilhado h3 {
    margin: 0 0 1.5rem 0;
    color: #1e3932;
    font-size: 1.563rem;
    font-weight: 100;
    font-family: 'SoDo Sans SemiBold', sans-serif;
}


.estilo_compartilhado p {
    font-size: 1.063rem;
    font-weight: 100;
    font-family: 'SoDo Sans SemiBold', sans-serif;
    text-align: center;
    line-height: 1.875rem;
    color: #1e3932
}

.btn_maior {
    border-radius: 3.125rem;
    padding: 0.375rem 0.375rem;
    font-family: 'SoDo Sans SemiBold';
    line-height: 1.2;
    border: 0.063rem solid #1e3932;
    background: none;
    box-shadow: none;
    font-size: 0.938rem;
    margin-top: 2.5rem;
    color: #1e3932;
    transition: 0.2s
}

.btn_maior:hover {
    transform: scale(0.9);
    box-shadow: 0.063rem 0.063rem 0.063rem rgba(0, 0, 0, .87);
}

.btn_compartilhado {
    border-radius: 3.125rem;
    padding: 0.5rem 0.625rem;
    font-family: 'SoDo Sans SemiBold';
    line-height: 1.2;
    border: 0.063rem solid #1e3932;
    background: none;
    box-shadow: none;
    font-size: 0.938rem;
    margin-top: 1.25rem;
    color: #1e3932;
    transition: 0.2s
}

.btn_compartilhado:hover {
    transform: scale(0.9);
    box-shadow: 0.063rem 0.063rem 0.063rem rgba(0, 0, 0, .87);
}

.layout_compartilhado {
    display: flex;
    flex-direction: row;
    margin: 2rem auto 0 auto;
    height: 26.25rem;
    width: 90rem;
}

.dimensionamentoCompartilhado {
    width: 45rem;
    height: 26.25rem;
    margin: 0 auto 0 auto;
}

.divParaImagem2 {
    width: 45rem;
    height: 26.25rem;
}

.divParaImagem2 img {
    width: 45rem;
    height: 26.25rem;
}

footer {
    box-shadow: 0px -0.313rem 0.313rem -0.313rem rgba(0, 0, 0, 0.5);
    width: 100vw;
}

hr {
    margin-left: 6.875rem;
    width: 90%;
    color: #fefefe;
}

.redes_sociais {
    margin-left: 6.875rem;
}

.redes_sociais img {
    margin: 2.813rem 0.938rem 1.875rem 0;
    transition: 0.2s;
}

.redes_sociais img:hover {
    transform: scale(1.2);
}

.politicas {
    display: flex;
    flex-direction: row;
    margin-left: 6.25rem;
    font-family: 'SoDo Sans SemiBold';
    font-weight: 100;
    font-size: 0.875rem;
}

.politicas p {
    margin: 0 0.625rem;
}

.corporation {
    margin: 2.813rem 0 3.75rem 6.25rem;
    font-family: 'SoDo Sans SemiBold';
    font-weight: 100;
    font-size: 0.875rem;
    color: rgba(0, 0, 0, .56);
}

