@import url(//db.onlinewebfonts.com/c/d7e8a95865396cddca89b00080d2cba6?family=SoDo+Sans+SemiBold);

* {
  margin: 0;
  padding: 0;
}

nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100vw;
  height: 6.219rem;
  box-shadow: 0 0.125rem 0.188rem rgba(0, 0, 0, .1);
}

nav img {
  width: 3.188rem;
  height: 3.188rem;
  margin: 0 2.5rem 0 2.5rem;
}

.div1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-transform: uppercase;
  font-family: 'SoDo Sans SemiBold';
  height: 3.188rem;
}

.div1 li {
  display: inline;
  list-style: none;
  font-weight: bold;
  font-size: 0.813rem;
  color: rgba(0, 0, 0, .87);
  font-size: 0.813rem;
  letter-spacing: 1px;
  margin: 0 1.438rem 0.5rem 0;

}

.div1 li:hover {
  color: rgb(4, 152, 4);
  cursor: pointer;
}

.div2 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 3.188rem;
  width: 34.063rem;
  margin-right: 2.5rem;
}

.historias_latinas {
  width: 7.5rem;
  margin: 0;
  padding: 0;
}

.btn_entrar {
  border-radius: 3.125rem;
  padding: 0.375rem 1rem;
  font-family: 'SoDo Sans SemiBold';
  color: rgba(0, 0, 0, .87);
  line-height: 1.2;
  background: none;
  border: 0.063rem solid rgba(0, 0, 0, .87);
  box-shadow: none;
  font-size: 0.938rem;
  margin-right: 0.25rem;
  transition: 0.2s;

}

.btn_entrar:hover {
  transform: scale(0.9);
  box-shadow: 0.063rem 0.063rem 0.063rem rgba(0, 0, 0, .87);
  background-color: rgb(229, 227, 227);
}

.btn_participe {
  border-radius: 3.125rem;
  padding: 0.375rem 1rem;
  font-family: 'SoDo Sans SemiBold';
  line-height: 1.2;
  background-color: rgba(0, 0, 0, .87);
  border: 0.063rem solid rgba(0, 0, 0, .87);
  box-shadow: none;
  color: #ffffff;
  font-size: 15px;
  transition: 0.2s;
}

.btn_participe:hover {
  transform: scale(0.9);
  box-shadow: 0.063rem 0.063rem 0.063rem rgba(0, 0, 0, .87);
}

.encontreUmaLoja {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: 'SoDo Sans SemiBold';
  font-size: 0.906rem;
  font-weight: 500;
  color: rgba(0, 0, 0, .87);
}

.encontreUmaLoja img {
  width: 1.313rem;
  height: 1.313rem;
  margin: 0.313rem;
}

